/**
  超出范围显示...
 */
.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal; }
.mult-ellipsis {
  position: relative;
  line-height: 24px;
  max-height: 72px;
  overflow: hidden; }
.mult-ellipsis:after {
    content: "...";
    position: absolute;
    bottom: 0;
    right: 0;
    padding-left: 30px;
    background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(55%, #fff));
    background: linear-gradient(to right, transparent, #fff 55%); }
